<template>
  <v-app @drop.native.prevent="dropFiles" @dragover.native.prevent @dragleave.native.prevent>
    <v-app-bar app color="primary" dark>
      <v-app-bar-nav-icon></v-app-bar-nav-icon>

      <v-toolbar-title>Session Viewer</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn href="https://github.com/filonik" target="_blank" text>
        <span class="mr-2">Latest Release</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
    </v-app-bar>

    <v-content>
      <v-container v-if="session" fluid fill-height style="align-items:start">
        <v-row no-gutters>
          <v-col>
            <v-card color="primary" dark>
              <v-card-title>
                {{ session.name }}
                <v-spacer></v-spacer>
                <v-text-field
                  v-model="session.search"
                  :append-icon="session.search ? 'mdi-close' : ''"
                  append-outer-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                  @click:append="session.search = ''"
                ></v-text-field>
              </v-card-title>
              <v-tabs v-model="window" center-active background-color="primary" dark>
                <v-tab
                  v-for="(item, key, index) in session.windows"
                  :key="key"
                >Window {{index+1}} ({{getFilteredTabCount(key)}})</v-tab>
                <!--</v-tabs>-->
                <!--<v-tabs-items v-model="window">-->
                <v-tab-item
                  v-for="(item, key) in session.windows"
                  :key="key"
                  :transition="false"
                  :reverse-transition="false"
                  :eager="true"
                >
                  <v-card flat>
                    <v-data-table
                      class="truncate"
                      :options="options"
                      :headers="headers"
                      :footer-props="footerProps"
                      :items="getFilteredTabs(key)"
                      :sort-by.sync="session.sortBy"
                      :sort-desc.sync="session.sortDesc"
                      dense
                    >
                      <template v-slot:header.title="{ header }">
                        <v-icon
                          size="18"
                          @click.stop="header.filterable = !header.filterable"
                        >{{ header.filterable? "mdi-filter" : "mdi-filter-outline"}}</v-icon>
                        {{ header.text }}
                      </template>

                      <template v-slot:header.url="{ header }">
                        <v-icon
                          size="18"
                          @click.stop="header.filterable = !header.filterable"
                        >{{ header.filterable? "mdi-filter" : "mdi-filter-outline"}}</v-icon>
                        {{ header.text }}
                      </template>

                      <template v-slot:header.host="{ header }">
                        <v-icon
                          size="18"
                          @click.stop="header.filterable = !header.filterable"
                        >{{ header.filterable? "mdi-filter" : "mdi-filter-outline"}}</v-icon>
                        {{ header.text }}
                      </template>

                      <template v-slot:item.favIconUrl="{ item }">
                        <img v-if="item.favIconUrl" :src="item.favIconUrl" width="16" height="16" />
                      </template>

                      <template v-slot:item.host="{ item, header }">{{header.getter(item)}}</template>

                      <template v-slot:item.url="{ item }">
                        <a :href="item.url" target="_blank">{{ item.url }}</a>
                      </template>

                      <template v-slot:item.lastAccessed="{ item, header }">{{header.getter(item)}}</template>
                    </v-data-table>
                  </v-card>
                </v-tab-item>
                <!--</v-tabs-items>-->
              </v-tabs>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <v-container v-else fluid fill-height style="align-items:center">
        <v-row no-gutters justify="center" align="center">
          <span>Drop Firefox session file here.</span>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
//import HelloWorld from "./components/HelloWorld";

function itemgetter(key) {
  return function(obj) {
    return obj[key];
  };
}

export default {
  name: "App",

  components: {
    //HelloWorld
  },

  data: () => ({
    session: null,
    window: null,
    options: {
      page: 0,
      itemsPerPage: 50,
      sortBy: [],
      sortDesc: [],
      groupBy: [],
      groupDesc: [],
      multiSort: false,
      mustSort: false
    },
    headers: [
      {
        text: "Icon",
        value: "favIconUrl",
        getter: itemgetter("favIconUrl"),
        sortable: false,
        filterable: false,
        width: "16px"
      },
      {
        text: "Title",
        value: "title",
        getter: itemgetter("title"),
        sortable: false,
        filterable: true,
        width: "40%"
      },
      {
        text: "URL",
        value: "url",
        getter: itemgetter("url"),
        filterable: false,
        width: "20%"
      },
      {
        text: "Host",
        value: "host",
        getter: item => new URL(item.url).hostname,
        filterable: false,
        width: "20%"
      },
      {
        text: "Last Accessed",
        value: "lastAccessed",
        getter: item => new Date(item.lastAccessed).toISOString(),
        filterable: false,
        width: "20%"
      }
    ],
    footerProps: {
      "items-per-page-options": [10, 20, 50, 100, -1]
    }
  }),

  methods: {
    getFilteredTabs(key) {
      function constant(x) {
        return function() {
          return x;
        };
      }
      //function search(x, y) {
      //  return x.includes(y);
      //}
      function search(y) {
        y = y.toLowerCase();
        return function(x) {
          x = x.toLowerCase();
          return x.includes(y);
        };
      }
      let result = Object.values(this.session.windows[key]);
      if (this.session.search) {
        let match = search(this.session.search);
        let predicate = this.headers
          .filter(header => header.filterable)
          .map(header => item => match(header.getter(item)))
          .reduce((acc, f) => item => acc(item) || f(item), constant(false));
        result = result.filter(predicate);
      }
      if (this.session.sortBy && this.session.sortBy.length) {
        const sortBy = this.session.sortBy[0];
        const sortDesc = this.session.sortDesc[0];
        console.log("Sort", sortBy, sortDesc);
        let header = this.headers.find(header => header.value == sortBy);
        result = result.sort((x, y) => {
          let sx = header.getter(x);
          let sy = header.getter(y);
          let sr = sx.localeCompare(sy);
          return sortDesc ? -sr : +sr;
        });
      }
      this.session.tabCounts[key] = result.length;
      return result;
    },
    getFilteredTabCount(key) {
      return this.session.tabCounts[key];
    },
    loadSession(file) {
      //console.log("Loading", file);
      var reader = new FileReader();

      // Closure to capture the file information.
      reader.onload = e => {
        let data = JSON.parse(e.target.result);

        let windows = data[0].windows;

        let tabCounts = {};
        for (let key of Object.keys(windows)) {
          tabCounts[key] = Object.keys(windows[key]).length;
        }

        this.session = {
          name: file.name,
          search: "",
          sortBy: [],
          sortDesc: [],
          windows,
          tabCounts
        };
      };

      // Read in the image file as a data URL.
      reader.readAsText(file);
    },

    dropFiles(event) {
      let droppedFiles = event.dataTransfer.files;

      if (!droppedFiles) return;

      let file = droppedFiles[0];

      this.loadSession(file);
    }
  }
};
</script>


<style>
.truncate td {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 0px;
}
</style>